@use '../abstracts/mixins' as mix;
@use '../abstracts/variables' as v;
@use '../abstracts/breakpoints' as brk;

.menu-container {
	@include mix.flex-center-column;
	width: 32.7rem;
	height: 42.9rem;

	@include brk.medium-screen {
		width: 46rem;
		height: 47.1rem;
	}

	.xo-header-container {
		@include mix.flex-center;
	}

	.pick-container {
		@include mix.flex-center-column;
		background-color: v.$color-blue;
		width: 100%;
		height: 20.5rem;
		border-radius: 1.5rem;
		margin-top: 3.2rem;
		text-align: center;
		padding: 2.4rem;

		@include brk.medium-screen {
			margin-top: 4rem;
		}
	}

	button {
		line-height: 2rem;
		letter-spacing: 1px;
		font-weight: 700;
		font-size: 1.6rem;
		width: 100%;
		height: 5.6rem;
		@include brk.medium-screen {
			font-size: 2rem;
			line-height: 2.5rem;
			letter-spacing: 1.25px;
		}
	}

	.new-game-vs-pc-button {
		@include mix.menu-button;
		background-color: v.$color-dark-orange;
		margin-top: 1.6rem;

		@include brk.medium-screen {
			margin-top: 2rem;
		}
		&:hover {
			cursor: pointer;
			background-color: v.$color-light-orange;
		}

		&:active {
			@include mix.clicked-button(0.1rem);
		}
	}

	.difficulty-container {
		display: flex;
		margin-top: 3.2rem;
		width: 100%;
		height: 5.6rem;
		border-radius: 1.5rem;
		overflow: hidden;
		@include brk.medium-screen {
			margin-top: 4rem;
		}
	}
}

.error-message {
		@include mix.flex-center-column;
		position: absolute;
		z-index: 2;
		background-color: v.$color-soft-red;
		width: 32.7rem;
		height: 10rem;
		border-radius: 0.5rem;
		margin-top: 3.2rem;
		text-align: center;
		align-self: flex-start;
		padding: 0.4rem;

		@include brk.medium-screen {
			width: 46rem;
		}

		.close-error {
			@include mix.reset-button;
			background-color: transparent;
			position: absolute;
			align-self: flex-end;
			translate: 0 -3.25rem;

			&:hover {
				cursor: pointer;
			}

			&:active {
				@include mix.clicked-button(-3.2rem)
			}

			 img {
				width: 2rem;
				height: 2rem;
			 }
		}

	p {
		color: black;
		font-weight: 700;
		font-size: 1.6rem;
		line-height: 2rem;
		letter-spacing: 1px;
	}
}

.difficulty-container {
	button {
		@include mix.reset-button;
		background-color: v.$color-dark-green;
		&:hover {
			cursor: pointer;
			background-color: v.$color-white;
		}

		&:active {
			@include mix.clicked-button(0.05rem);
		}
	}

	.selected-difficulty {
		background-color: v.$color-light-green;

		&:hover {
			cursor: pointer;
			background-color: v.$color-light-green;
		}
	}
}



.xo-header-container {
	img {
		width: 3.2rem;
		height: 3.2rem;
		background-color: transparent;
		margin-left: 0.8rem;
	}
}

.pick-container {
	color: v.$color-grey;
	h2 {
		font-weight: 700;
		font-size: 1.6rem;
		line-height: 2rem;
		letter-spacing: 1px;
	}

	.xo-button-container {
		@include mix.flex-center;
		width: 100%;
		height: 7.2rem;
		background-color: v.$color-dark-blue;
		margin: 2.4rem 0 1.7rem;
		padding: 0.8rem;
	}

	.remember-sentence {
		font-weight: 500;
		font-size: 1.4rem;
		line-height: 1.8rem;
		letter-spacing: 0.875px;
		opacity: 0.5;
	}
}

.xo-button-container {
	border-radius: 1rem;
	.x-button {
		@include mix.reset-button;
		width: 50%;
		height: 5.4rem;
		border-radius: 1rem;
		background-color: v.$color-dark-blue;
		&:hover {
			cursor: pointer;
			background-color: v.$color-blue;
		}
	}

	.o-button {
		@include mix.reset-button;
		width: 50%;
		height: 5.4rem;
		background-color: v.$color-dark-blue;
		border-radius: 1rem;
		&:hover {
			cursor: pointer;
			background-color: v.$color-blue;
		}
	}

	img {
		width: 3.2rem;
		height: 3.2rem;
		color: v.$color-grey;
	}
}
